<template>
    <div :style="style1" class="ccui-top-nav-menu3">
        <div class="sub-menu-wrapper-wrapper">
            <div :style="style" class="sub-menu-wrapper">
                <div class="logo-container">
                    <router-link :to="dashboard" v-if="dashboard">
                        <img :src="logo"
                             alt=""
                             class="src" style="height: 40px; width: 40px; border: 0;">
                        <span class="app-name">
              <p class="appTitle">{{appTitle}}</p>
              <p :title="companyTitle" class="companyTitle">{{companyTitle}}</p>
            </span>
                    </router-link>
                    <div class="cursorPointer" v-if="!dashboard" style="display: flex;">
                        <img :src="logo"
                             alt=""
                             class="src" style="height: 40px; width: 40px; border: 0;">
                        <span class="app-name">
              <p class="appTitle">{{appTitle}}</p>
              <p :title="companyTitle" class="companyTitle">{{companyTitle}}</p>
            </span>
                    </div>
                </div>
                <ul class="nav">
                    <li @click="skip(item)" class="nav-item" v-for="(item, index) in subMenu1">
                        <router-link :redirect="item.m" :to="{ path: item.url}">
                            <span>{{item.name}}</span>
                        </router-link>
                    </li>
                </ul>
                <i @click="ccuiHelp" class="icon-ccui-help"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'top-nav-menu3',
  props: {
    appTitle: String,
    companyTitle: String,
    minWidth: {
      type: String,
      default: '1180px'
    },
    Width: {
      type: String,
      default: '1080px'
    },
    width: {
      type: String,
      default: 'auto'
    },
    maxWidth: String,
    logo: {
      type: String,
      default: 'data:image/png;base64,'
    },
    dashboard: String,
    subMenu: Array,
    defaultIndex: String
  },
  data () {
    return {
      visible: false,
      subMenu1: [],
      subMenu2: []
    }
  },
  mounted () {
    this.subMenu1 = this.subMenu
    if (this.subMenu.length > 8) {
      this.subMenu2 = this.subMenu.slice(9)
      this.subMenu1 = this.subMenu.slice(0, 9)
    } else {
      this.subMenu1 = this.subMenu
    }
  },
  methods: {
    skip (item) {
      this.$emit('onSubmitSkip', item)
    },
    ccuiHelp (item) {
      this.$emit('ccuiHelp', item)
    }
  },
  watch: {
    subMenu () {
      console.log('111111', this.subMenu)
      this.subMenu1 = this.subMenu
      if (this.subMenu.length > 8) {
        this.subMenu2 = this.subMenu.slice(9)
        this.subMenu1 = this.subMenu.slice(0, 9)
      } else {
        this.subMenu1 = this.subMenu
      }
    }
  },
  computed: {
    style () {
      let style = {}
      style.margin = '0 auto'
      style.maxWidth = '1730px'
      return style
    },
    style2 () {
      let style = {}
      style.height = '80px'
      style.margin = '0 auto'
      style.maxWidth = '1740px'
      style.paddinTop = '15px'
      return style
    },
    style1 () {
      let style1 = {}
      style1.minWidth = this.minWidth
      style1.width = this.width
      style1.margin = ' 0 auto'
      return style1
    }
  }
}
</script>
<style scoped>
    body, button, dd, dl, dt, h1, h2, h3, h4, h5, h6, hr, input, li, ol, p, pre, td, textarea, th, ul {
        margin: 0;
        padding: 0;
    }
    ul, li {
        list-style: none;
    }
    .nav {
        display: flex;
        line-height: 80px;
        padding: 0;
        margin: 0;
        margin-left: 201px;
    }
    .nav li a {
        padding: 0 15px;
        line-height: 80px;
        display: block;
        color: #fff;
        font-size: 16px;
        text-align: center;
        font-weight: bolder;
        height: 77px;
    }
    .nav li a:hover {
        background-color: #007bd7;
        border-bottom: 3px solid #007bd7;
        box-sizing: border-box;
    }
    .nav li a span{
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        min-width: 54px;
        display: inline-block;
    }

    .nav li .router-link-active {
        background-color: #007bd7;
        border-bottom: 3px solid #fff !important;
        box-sizing: border-box;
    }
    .sub-menu-wrapper-wrapper {
        padding-left: 50px;
        background-color: #0089EF;
        position: relative;
    }
    .el-top-nav-menu3 .sub-menu-wrapper-wrapper .sub-menu-wrapper {
        position: relative;
        margin: 0 auto;
        height: 80px;
    }
    .el-top-nav-menu3 .sub-menu-wrapper-wrapper .sub-menu-wrapper .logo-container {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        width: 200px;
        z-index: 1000;
        font-size: 0;
    }
    .logo-container {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        width: 200px;
        z-index: 1000;
        font-size: 0;
    }
    .sub-menu-wrapper .logo-container img {
        display: inline-block;
        vertical-align: middle;
    }
    .sub-menu-wrapper .logo-container span.app-name {
        font-size: 16px;
        color: #FFFFFF;
        font-weight: 600;
        vertical-align: middle;
        margin-left: 5px;
        width: 70%;
    }
    .sub-menu-wrapper .logo-container .app-name .appTitle {
        font-size: 16px;
        font-weight: 600;
        color: white;
        line-height: 22px;
    }
    .sub-menu-wrapper .logo-container .app-name .companyTitle {
        font-size: 12px;
        font-weight: 400;
        color: white;
        line-height: 17px;
    }
    .sub-menu-wrapper .logo-container {
        display: inline-block;
    }
</style>

